import { userLogin } from "./user";

export const SEND_LOGIN_REQUEST = "SEND_LOGIN_REQUEST";
export const SEND_LOGIN_SUCCESS = "SEND_LOGIN_SUCCESS";
export const SEND_LOGIN_ERROR = "SEND_LOGIN_ERROR";

export const submitLoginForm = (username, password) => {
  return dispatch => {
    if (username === "aaaaaa" && password === "11111111") {
      return dispatch(userLogin(username));
    } else {
        return dispatch()
    }
  };
};



